 0;\n  background-color: $color-brand-secondary;\n  outline: 0;\n  cursor: pointer;\n}\n\nselect::-ms-expand {\n  display: none;\n}\n\noption:first-of-type {\n  display: none;\n}\n","$color-brand-primary: #00bcd4;\n$color-brand-secondary: #00bcd429;\n\n$color-page-background: #f9f9f9;\n\n$color-app-border: #0000001f;\n$color-app-background: #ffffff;\n\n$color-grid-border: #dcdcdc;\n\n$color-input-disabled: #e0e0e0;\n$color-input-background: #b4b4b4;\n$color-input-placeholder: #b8b8b8;\n\n$color-button-default: #f5f5f5;\n$color-button-text-color: #ffffff;\n$color-button-black-color: #000000;\n",".product-addition {\n  &__input {\n    width: 120px;\n  }\n}\n\n.product-inventory {\n  &__container {\n    grid-template-columns: repeat(5, 1fr);\n    margin-bottom: 48px;\n  }\n\n  &__input {\n    width: 87px;\n    border: 0;\n    text-align: center;\n    outline: 0;\n  }\n}\n",".coin-charge {\n  &__input {\n    width: 300px;\n  }\n\n  &__form {\n    margin-bottom: 20px;\n  }\n}\n\n.coin-holdings {\n  width: 237px;\n  margin: auto;\n}\n",".money-charge {\n  &__input {\n    width: 300px;\n  }\n\n  &__form {\n    margin-bottom: 20px;\n  }\n}\n\n.product-menu {\n  &__container {\n    grid-template-columns: repeat(4, 1fr);\n    margin-bottom: 60px;\n  }\n}\n\n.coin-return {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 237px;\n  margin: auto;\n\n  &__container {\n    width: 100%;\n    margin-bottom: 16px;\n  }\n\n  &__button {\n    width: 100px;\n    height: 32px;\n    margin-bottom: 36px;\n  }\n}\n",".login-form {\n  display: flex;\n  flex-direction: column;\n  &__email-input {\n    margin-bottom: 16px;\n  }\n\n  &__pw-input {\n    margin-bottom: 30px;\n  }\n\n  &__button {\n    width: 300px;\n    margin-left: 0;\n    margin-bottom: 14px;\n  }\n}\n\n.signup {\n  &-button {\n    color: #3581d7;\n    background-color: transparent;\n    margin-left: 10px;\n  }\n}\n","@keyframes fadeIn-out {\n  0% {\n    visibility: visible;\n    opacity: 0;\n  }\n